<template>
  <div class="hello">
    <div class='topImg'>
        <img src="../assets/home/top.jpg">
        <div class="logo">
          <img src="../assets/home/logo.png">
        </div>
        <div class="topTime" v-html='time'></div>
    </div>
    <el-menu theme="dark" :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
        <el-menu-item index="1">三叉神经痛的伽玛刀治疗</el-menu-item>
        <el-menu-item index="2">设备介绍</el-menu-item>
        <el-menu-item index="3">视频介绍</el-menu-item>
        <el-menu-item index="4">联系我们</el-menu-item>
    </el-menu>
    <router-view></router-view> 
  </div>
</template>

<script>

export default {
    data() {
      return {
        activeIndex: '1',
        time:''
      };
    },
    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
        if( key == 1 ){
          this.$router.push({path:'/hello/cure'})
        }else if( key == 2 ){
           this.$router.push({path:'/hello/device'})
        }else if( key == 3 ){
          this.$router.push({path:'/hello/vd'})
        }else{
           this.$router.push({path:'/hello/contact'})
        }

      },
      topTime(){
        let week = ["星期日", "星期一", "星期二", "星期三", "星期四", "星期五", "星期六"];
        let localToday = '';
        let d = new Date();
        let y = d.getFullYear();
        let m = d.getMonth()+1;
        let dt = d.getDate();
        let h = d.getHours();
        let mt = d.getMinutes();
        let sd = d.getSeconds();
        let wk = week[d.getDay()];
        if( sd < 10){
          sd = '0'+sd;
        }
        if( mt < 10){
          mt = '0'+mt;
        };
        let localDay = y+'年'+m+'月'+dt+'日'+'&nbsp;'+h+':'+mt+'&nbsp;&nbsp;&nbsp;'+wk;
        this.time = localDay
        console.log(d.getMinutes())
      }
    },
    mounted(){
      this.topTime();
    }
  }


</script>

<style scoped>
.hello{
  padding-bottom: 80px;
}
.topImg{
  position: relative;
  width: 100%;
}
.topImg img{
  width: 100%;
  display: block;
}
.logo{
  position: absolute;
  top: 40px;
  left: 50px;
  width: 330px;
  height: 69px;
}
.topImg>div img{
  width: 100%;
  display: block;
}
.el-menu--dark,.el-menu{
  /*position: relative;
  bottom: 40px;*/
  padding: 0 220px;
  /*padding: 0 320px;*/
}
.el-menu--horizontal .el-menu-item{
  width: 180px;
  text-align: center;
}
.topTime{
  position: absolute;
  bottom: -40px;
  right: 80px;
  width: 240px;
  text-align: center;
  color: #bfcbd9;
  /*color: #1D8CE0;*/
  z-index: 999;
}
</style>
<style>
   .el-menu--horizontal .el-menu-item{
   width: 140px;
  text-align: center;
}
</style>



