import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Vd from '../components/more/vd'
import Cure from '../components/more/cure'
import Device from '../components/more/device'
import Contact from '../components/more/contact'

Vue.use(Router)

export default new Router({
  routes: [
    {path:'/',redirect: '/hello/cure'},
    {
      path: '/hello',
      component: Hello,
      children:[
      {
      	path:'/hello/vd',
      	component: Vd
      },
      {
        path:'/hello/cure',
        component:Cure
      },
      {
        path:'/hello/device',
        component:Device
      },
      {
        path:'/hello/contact',
        component:Contact
      }
      ]
    }
  ]
})



